.menu {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  padding: 25px;
  color: #fff;
  grid-area: menu;
  
}

.review-con {
    /* text-align: center; */
    width: 353px;
    margin-left: auto;
    margin-right: auto;
}

.menu .menu_block {
    color: white;
    list-style-type: none;
    font-size: 17px;
    text-transform: uppercase;
    margin-left: 10px;
    margin-right: 10px;
    text-decoration:none;
    animation: 1s;
    animation-delay: 250ms;

}
.menu .menu_block:hover{
	padding-bottom: 3px;
	border-bottom: 2px solid #fac51c;
  transition: 0.05s
  
}
.menu .menu_block:not(:hover){
	padding-bottom: 3px;
	border-bottom: 0px solid #fac51c;
  transition: 0.05s
  
}
#root {
  height: 100vh;
  display: grid;
  grid-template-areas: 'menu menu'
  'content   content'
  'footer footer';
  grid-template-columns: 100% 1fr;
  grid-template-rows: 3rem 1fr 3rem;
}

.root:before {
  content: "";
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  opacity: 0.3;
}
